@import "../../styles/globalvar";

.wrapper__main {
  max-width: 960px;
  margin: 0 auto;

}

.header {
  background-color: $backgroundPurple;
  height: 50px;


  &__inner {
    display: flex;
    justify-content: space-between;
  }

  .nav__list {
    display: flex;

  }

  .nav__item {
    margin: 0 10px;

    .nav__item--link {
      font-family: Roboto, sans-serif;
      font-size: 14px;

      &:hover {
        color: $colorhref;
      }
    }
  }


}

.header__content {
  background-image: url('../../images/headerbg.png');
  background-size: cover;
  height: 620px;


}

.content__inner {
  display: flex;
  flex-direction: column;
  align-items: center;

}

.header__content {
  padding-top: 160px;
}

.header__title {

  font-family: $FPD;
  font-size: 32px;
  color: white;
  position: relative;

}

.header__text {
  text-align: center;
  max-width: 775px;
  color: white;
}

@media screen and (max-width: 768px) {
  .nav__item {
    display: none;
  }
}

